@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
body,html {
  width: 100vw;
  height: 100vh;
  margin: 0;
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  cursor: default;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* Color Palettes */
:root{
  --app-background: #313740;
  --app-column-counter: #1e2128;
  --app-row-counter: #252a32;
  --app-menu: #36383d;
  --app-menu-text: #fbfbfb;
  --app-popup-top: #323942;
  --app-popup-text: #2c3039;
  --app-popup-background: #fefefe;
  --element-dimension: 68px;
}

*{
  box-sizing: border-box;
}

.App {
  width: 100vw;
  height: 100vh;
  background: #313740;
  background: var(--app-background);
  color: #fbfbfb;
  color: var(--app-menu-text);
  overflow: auto;
}

/* centers */
.centroid {
  max-width: 1440px;
  margin: auto;
  padding: 10px;
}

.workarea{
  /* component styles will be here */
  padding: 15px;
}

.workarea > * {
  transition: 0.3s ease;
  -webkit-transition: 0.3s ease;
}

.workarea-periodic-table{
  display: grid;
  place-items: center;
}


.periodic-table{
    display: grid;
    grid-template-columns: repeat(18, var(--element-dimension));
    grid-template-rows: repeat(10, var(--element-dimension));
    grid-gap: 4px;
  }
  
  .element{
    background: var(--app-menu);
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.3);
    border-radius: 2px;
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
  }
  
  .element-atomic-no{
    font-weight: 400;
    font-size: 10px;
    position: absolute;
    top: 7px;
    left: 7px;
    display: table;
    flex: 0 1;
  }
  
  .element-symbol{
    font-size: 25px;
    padding-top: 7px;
    font-weight: 700;
    padding-top: 15px;
    flex: 6 1;
  }
  
  .element-name{
    font-size: 9px;
    letter-spacing: 0.54px;
    flex: 3 1;
    text-transform: capitalize;
  }


.navbar{
  height: 50px;
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background: var(--app-popup-top);
  box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.3);
}

.navbar-item{
  /* center this item top to bottom */
}

.navbar-name{
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
}
